.sidebar {
    background-color: #1e1f1c;
    box-shadow: 5px 0 15px rgba(5,5,5,.1);
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 300px;
    z-index: 1000;
}

.profile {
    margin: 0 auto;
    width: 90%;
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.avatar {
    width: 130px;
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin-bottom: 10px;
    overflow: hidden;
    background-color: #414339;
}

.profile img {
    display: block;
    width: 130px;
    height: 130px;
}

.social {
    display: flex;
    flex-direction: column;
}

.social_links {
    width: 95%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: space-around;
    margin-bottom: 20px;
    background-color: #414339;
    border-radius: 3px;
    padding: 7px;
}

.social__item {
    width: 20%;
    height: 20%;
    margin-left: 8px;
    
}

.social__item a img {
    width: 100%;
    height: 100%;
    display: block;
    
}

.social_text {
    margin-bottom: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.technologies_title {
    font-weight: bold;
    margin: 10px;
}

.profile__skills {
    width: 95%;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.profile__skills__box {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    background-color: #414339;
    border-radius: 3px;
    padding: 7px;
}

.profile__skills__item {
    width: 20%;
    height: 20%;
    margin: 5px;
}

.profile__skills__item img {
    display: block;
    width: 100%;
    height: 100%;
}

@media screen
and (max-width : 576px) 
{
    .sidebar {
        position: static;
        width: 100vw;
    }

    .profile {
        width: 100%;
        margin-top: 2rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .avatar {
       margin-top: 10px;
    }

    .profile__skills__item {
        width: 17%;
        height: 17%;
        margin: 4px;
    }

    .profile__skills__box {
        padding: 10px;
    }

    .profile__skills {
        width: 75%;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
    }
}